'use strict'

let ABC_CONSTANT = require('./constants');
let axios = require('axios');

let backboneTokenInfo =  {}; 
let cToolTokenInfo =  {}; 
let mcpTokenInfo =  {}; 

/**
 * Default Handler for Each Network request
 * @param {*} response 
 */
let defaultResponseHandler = function(response) {
    return response.data ? response.data : {};
}

/**
 *  Default Handler for Error from Network requests
 * @param {*} error 
 * @returns {} Error Object{ error: true, status: , data: , message?: };
 */
let defaultErrorHandler = function(error) {
    var errorResponse = {};
    if (error.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      errorResponse = {error: true, result: "error", status: error.response.status, data: error.response.data};
    } else {
      // Something happened in setting up the request that triggered an Error
      // OR 
      // The request was made but no response was received
      errorResponse = {error: true, result: "error", status: null, data: null, message: error.message};
    }
    if(ABC_CONSTANT.debug && console){
        console.error('ABC:ERROR', JSON.stringify(errorResponse));
    }
    return errorResponse;
}

/**
 * 
 * @param {Error} e 
 */
let defaultValidationErrorHandler = function(e){
    return Promise.resolve({error: true, result: "error", status: null, data: null, message: e.message, validationError: true});
}

/**
 * Default Handler for all GET request
 * @param {String} url 
 * @param {Object} requestData 
 * @param {Object} headers 
 */
let makeGETRequest = function(url, requestData, headers){
    let params = requestData ? requestData : {};
    // Logic for cache busting
    params._ = (new Date()).getTime();
    let config = {
        params: params
    };
    // pass headers if exists
    if(headers){
        config.headers = headers;
    }
    return axios.get(url, config)
        .then(defaultResponseHandler)
        .catch(defaultErrorHandler);
}

/**
 * Default Handler for all POST request
 * @param {String} url 
 * @param {Object} requestPayload 
 * @param {Object} headers 
 */
let makePostRequest = function(url, requestPayload, headers){
    let config = {};
    // pass headers if exists
    if(headers){
        config.headers = headers;
    }
    return axios.post(url, requestPayload, config)
        .then(defaultResponseHandler)
        .catch(defaultErrorHandler);
}

/**
 * Default Handler for all DELETE requests
 * @param {String} url 
 * @param {Object} requestData 
 * @param {Object} headers 
 */
let makeDeleteRequest = (url, requestData, headers) => {
    let config = requestData ? {
        params: requestData
    } : {};
    // pass headers if exists
    if(headers){
        config.headers = headers;
    }
    return axios.delete(url, config)
        .then(defaultResponseHandler)
        .catch(defaultErrorHandler);
}

/**
 * Get Backbone server Token Info
 * @param {Object} init 
 */
let getBackboneToken = function(init){

    // use existing token if not expired
    //@todo: clear this token on logout
    if (init.cacheToken && backboneTokenInfo.tokenExpiry && (new Date()).getTime() < backboneTokenInfo.tokenExpiry) {
        return Promise.resolve(backboneTokenInfo);
    }

    return makeGETRequest(init.baseUrl + init.restPrefix + init.getBackboneTokenURL).then(function (_data) {
        backboneTokenInfo = _data; 
        return _data; 
    }); 
}

/**
 * Request Backbone server with Auth Headers
 * @param {String} url 
 * @param {Object} requestData 
 */
let makeBackboneGETRequest = function(url, requestData){

    return getBackboneToken(ABC_CONSTANT).then(function(tokenInfo){
        let headers = {
            "Authorization": 'Bearer ' + tokenInfo.access_token,
            "x-abc-workspace": tokenInfo.bbWorkspace,
            "x-abc-workspace-env": tokenInfo.bbWorkspaceEnv
        };
        return makeGETRequest(url, requestData, headers);
    });
}

/**
 * Request POST call to Backbone server with Auth Headers
 * @param {String} url 
 * @param {Object} requestPayload 
 */
let makeBackbonePostRequest = function(url, requestPayload){

    return getBackboneToken(ABC_CONSTANT).then(function(tokenInfo){
        let headers = {
            "Authorization": 'Bearer ' + tokenInfo.access_token,
            "x-abc-workspace": tokenInfo.bbWorkspace,
            "x-abc-workspace-env": tokenInfo.bbWorkspaceEnv
        };
        return makePostRequest(url, requestPayload, headers);
    });
}

/**
 * Fetch Ctool token
 *      Return one if already have and not expired
 * @param {Object} init 
 */
let getCToolToken = function(init){
        
    // use existing token if not expired
    //@todo: clear this token on logout
    if (init.cacheToken && cToolTokenInfo.tokenExpiry && (new Date()).getTime() < cToolTokenInfo.tokenExpiry) {
        return Promise.resolve(cToolTokenInfo);
    }

    return makeGETRequest(init.baseUrl + init.restPrefix + init.getCToolTokenURL).then(function (_data) {
        // making copy for caching until expires
        _data.apiUrl = _data.apiUrl ? _data.apiUrl.replace(/\/$/,'') + "/" : _data.apiUrl;
        cToolTokenInfo = _data; 
        return _data; 
    }); 
}

/**
 * Request GET call to Ctool server with Auth Header
 * @param {String} urlPath 
 * @param {Object} request 
 */
let makeCTGetRequest = function(urlPath, request){
    return getCToolToken(ABC_CONSTANT).then(function(ct_token){
        let headers = {
            "authorization": 'Bearer ' + ct_token.token
        };
        return makeGETRequest(ct_token.apiUrl + ct_token.workspace + "/" + urlPath, request, headers);
    });
}

/**
 * Request POST call to Ctool server with Auth Header
 * @param {String} urlPath 
 * @param {Object} payload 
 */
let makeCTPostRequest = function(urlPath, payload){
    return getCToolToken(ABC_CONSTANT).then(function(ct_token){
        let headers = {
            "authorization": 'Bearer ' + ct_token.token
        };
        return makePostRequest(ct_token.apiUrl + ct_token.workspace + "/" + urlPath, payload, headers);
    });
}

/**
 * Request DELETE call to Ctool server with Auth Header
 * @param {String} urlPath 
 * @param {Object} request 
 */
let makeCTDeleteRequest = (urlPath, request) => {
    return getCToolToken(ABC_CONSTANT).then(function(ct_token){
        let headers = {
            "authorization": 'Bearer ' + ct_token.token
        };
        return makeDeleteRequest(ct_token.apiUrl + ct_token.workspace + "/" + urlPath, request, headers);
    });
}

/**
 * Retrieve MCP Token
 * @param {Object} init 
 */
let getMCPToken = (init) => {
    // use existing token if not expired
    //@todo: clear this token on logout if not reloading
    if (init.cacheToken && mcpTokenInfo.tokenExpiry && (new Date()).getTime() < mcpTokenInfo.tokenExpiry) {
        return Promise.resolve(mcpTokenInfo);
    }

    return makeGETRequest(init.baseUrl + init.restPrefix + init.getMCPTokenURL).then(function (_data) {
        // making copy for caching until expires
        mcpTokenInfo = _data;
        return _data; 
    }); 
}

/**
 * Request GET call to MCP rule server
 * @param {String} url 
 * @param {Object} request 
 */
let makeMCPRuleGetRequest = function(url, request){
    return getMCPToken(ABC_CONSTANT).then(function(mcp_token){
        let headers = {
            "Authorization": 'Bearer ' + mcp_token.access_token
        };
        return makeGETRequest(url, request, headers);
    });
}

/**
 * 
 * @param {*} param 
 */
let buildInitParam = function(param){
    const init = Object.assign(ABC_CONSTANT, param);
    init.baseUrl = init.baseUrl.replace(/\/$/,'');
    init.restPrefix = init.restPrefix.replace(/\/$/,'') + "/";
    init.backboneGateway = init.backboneGateway.replace(/\/$/,'');
    init.ruleEngineGateway = init.ruleEngineGateway.replace(/\/$/,'') + "/";
    // Making updated variable globally avaialble within util
    ABC_CONSTANT = init;
    return init;
}

/**
 * return a string of random characters, 
 * dependent on type of characters to use.
 * 
 * @private
 * @param {number} len 
 * @param {number} type 
 * @returns {string}
 */
let stringGen = function(len, type){
    // type = 0 for Number
    // type = 1 for character
    var text = " ";
    var charset = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    if (type == 0) {
        charset = "0123456789";
    }
    for (var i = 0; i < len; i++){
        text += charset.charAt(Math.floor(Math.random() * charset.length));
    }
    return text;
};

/**
 * generateCustomerNumber
 * generate User friendly Customer Number
 * 
 * @returns {String} customerNumber
 */
let generateCustomerNumber = function(){
    var customerNumber = 'CN'+ stringGen(2, 1) + stringGen(1, 0) + stringGen(2, 1) + '-' + stringGen(2, 0) + stringGen(1, 1) + stringGen(2, 0) + '-' + stringGen(1, 0) + stringGen(1, 1) + stringGen(1, 0);
    return customerNumber.replace(/\s/g, "");
};

/**
 * generate User friendly Order Number
 * @returns {String} orderNumber
 */
let generateOrderNumber = function(){
    var orderNumber = 'ON'+ stringGen(2, 1) + stringGen(1, 0) + stringGen(2, 1) + '-' + stringGen(2, 0) + stringGen(1, 1) + stringGen(2, 0) + '-' + stringGen(1, 0) + stringGen(1, 1) + stringGen(1, 0);
    return orderNumber.replace(/\s/g, "");
}

/**
 * generate User friendly Line Item Number
 * @returns {String} lineItemNumber
 */
let generateLineItemNumber = function(){
    var lineItemNumber = 'IN'+ stringGen(2, 1) + stringGen(1, 0) + stringGen(2, 1) + '-' + stringGen(2, 0) + stringGen(1, 1) + stringGen(2, 0) + '-' + stringGen(1, 0) + stringGen(1, 1) + stringGen(1, 0);
    return lineItemNumber.replace(/\s/g, "");
}

/**
 * Search data set for key : value
 * Optimized version of forEach
 * Update: It can compare in case insensitive manner too
 * Update: Switched to for loop as it is faster than for in loop
 * 
 * @param {Array} dataSouce
 * @param {String} key
 * @param {any} value
 * @param {String} flag
 * 
 * @returns {Object}
 */
let searchFirstByKeyValue = function(dataSouce, key, value, flag){
    var row = {};
    for(var i = 0, len = dataSouce.length; i < len; i++){
        if (dataSouce[i][key] == value){
            row = dataSouce[i];
            break;
        }
        else if(flag && flag == 'i' && dataSouce[i][key] && (new String(dataSouce[i][key])).toLowerCase() == (new String(value)).toLowerCase() ){
            // case insensitive search
            row = dataSouce[i];
            break;
        }
    }
    return row;
};

/**
 * get datatype of property
 * 
 * @param {any} prop 
 * @param {string} type 
 * @returns {boolean}
 */
let typeOf = function(prop, type){
    const dataset = {
        'object': '[object Object]',
        'array': '[object Array]',
        'string': '[object String]',
        'boolean': '[object Boolean]',
        'number': '[object Number]',
        'date': '[object Date]',
        'undefined': '[object Undefined]',
        'null': '[object Null]',
    };

    return (Object.prototype.toString.call(prop) === dataset[type]);
}

/**
 * Object of Key Value pair
 * @param {Object} obj 
 */
let objectToSetOfStrings = function(obj){
    // Converting object to set of strings
    return Object.keys(obj).map(function(key) {
        return key + ':' + obj[key]
    });
}

/**
 * Convert Array into Object
 * @param {Array} arr 
 */
let setOfStringsToObject = function(arr){
    var obj = {};
    // Converting set of strings to Object
    for (var i = 0, len = arr.length; i < len; ++i){
        var set = arr[i].split(":");
        if(set.length){
            obj[set[0]] = set[1];
        }
    }
    return obj;
}

module.exports = {
    sendValidationError: defaultValidationErrorHandler,
    makeGETRequest: makeGETRequest,
    makePostRequest: makePostRequest,
    makeDeleteRequest,

    makeBackboneGETRequest: makeBackboneGETRequest,
    makeBackbonePostRequest: makeBackbonePostRequest,

    makeCTGetRequest: makeCTGetRequest,
    makeCTPostRequest: makeCTPostRequest,
    makeCTDeleteRequest,

    makeMCPRuleGetRequest,

    buildInitParam: buildInitParam,
    generateOrderNumber: generateOrderNumber,
    generateLineItemNumber: generateLineItemNumber,
    generateCustomerNumber: generateCustomerNumber,
    typeOf: typeOf,
    objectToSetOfStrings: objectToSetOfStrings,
    setOfStringsToObject: setOfStringsToObject,
    searchFirstByKeyValue: searchFirstByKeyValue,
};


