'use strict'

/**
 * Default value of init params, 
 * that can be injected while intializing any Abc cms Service component
 * 
 * Do not modify below properties, instead pass as param while intializing
 */
module.exports = {
	/**
	 * @type String
	 */
	baseUrl : '',

	/**
	 * @type Boolean
	 */
	debug: false,

	/**
	 * @type String
	 */
	backboneGateway : 'https://backbone.abc.cimpress.io',

	/**
	 * @type String
	 */
	ruleEngineGateway: 'https://rule.products.cimpress.io',

	/**
	 * {String} lang
	 */
	lang: 'en',
	
	/**
	 * Configuration option to disable Token caching in client services.
	 * i.e. define it to false if client service is not a global package on page.
	 * {Boolean} cacheToken
	 */
	cacheToken : true,

	/**
	 * API URL that returns backbone Gateway token
	 * @type String
	 */
	getBackboneTokenURL: 'abc-backbone/getToken',

	/**
	 * API URL that returns CTool token
	 * @type String
	 */
	getCToolTokenURL: 'abc-router/getToken',

	/**
	 * CMS API URL returns MCP token
	 * @type String
	 */
	getMCPTokenURL: 'abc-backbone/getMCPToken',

	/**
	 * rest endpoint prefix for CMS
	 * i.e. /.rest/ for magnolia
	 */
	restPrefix: '',

	/**
	 *  Adding below to avoid any unexpected exceptions
	 */
	_EventBus: {
		subscribe: function(){},
		dispatch: function(){},
		unsubscribe: function(){},
		subscriptions: function(){}, 
	},
};


